export default{
  data() {
    return {
      title: '',
      type: '',
      showInfo: false,
      code_word: '获取验证码',
      username: '',
      code: ''
    }
  },
  onLoad(option) {
    this.type = option.type
    if(option.type === 'register') {
      this.title = '设置密码'
      return
    }
    if(option.type === 'forget') {
      this.title = '重置密码'
      return
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1,
        "animationType": "zoom-fade-out"
      })
    },
    onInput(e) {
      let value = e.detail.value
      this.username = value
    },
    sureAction() {
      uni.switchTab({
        url: '../index/index'
      })
    }
    
  }
}
